.card-body {
    padding: 2em;
}

.bg-light, .content-wrapper {
    background: #FFF !important;
}

.card-header {
    border-radius: 28px !important;
    background: linear-gradient(to bottom right, #DC2027, #FF214F);
}

#mainNav .navbar-brand {
    background: linear-gradient(to bottom right, #DC2027, #FF214F);
}

#mainNav.navbar, .box {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

div.lr_icon>a>img {
    max-width: 70px;
}

.logo_icon {
    max-width: 50px;
    max-height: 50px;
}